.icon {
  height: 24px;
  width: 24px;
  display: block;
  background-size: cover;

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(../images/Icon-Phone-call_new.png);

    position: relative;

    @include on-desktop {
      &::before {
        content: '+1 234 5555-55-55';
        position: absolute;
        top: 50%;
        left: -20px;
        transform: translate(-100%, -50%);
        width: max-content;
        color: #fff;
        font-size: 12px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
        transition: all 0.3s;
        opacity: 0;
        z-index: -1;
      }

      &:hover::before {
        opacity: 1;
        z-index: 1;
      }
    }
  }

  &--menu {
    background-image: url(../images/Icon-Burger-menu_new.png);

    &:hover {
      background-image: url(../images/Icon-Burger-menu-hover.png);
    }
  }

  &--close {
    background-image: url(../images/Icon-Close_new.png);
  }
}
