@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin on-mobile {
  @media (max-width: $tablet-min-width) {
    @content;
  }
}

@mixin constent-padding-inline() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    margin-inline: auto;
    padding-inline: 120px;
  }
}

.container {
  @include constent-padding-inline;
}

@mixin hover {
  transition: transform 0.3s;
  &:hover {
    transform: scale(1.05);
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}
