.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  text-transform: uppercase;
  font-weight: 600;
  overflow: auto;
  transition: all 0.3s;
  opacity: 0;
  transform: translateX(-100%);
  pointer-events: none;
  position: fixed;

  &:target {
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
  }

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone {
    &-number {
      color: $color-menu-text;
      text-decoration: none;
      display: block;
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 27px;
    }
  }

  &__book {
    &-test-ride {
      display: block;
      width: fit-content;
      padding-bottom: 7px;
      border-bottom: 1px solid $color-menu-text;

      color: $color-menu-text;
      text-decoration: none;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
    }
  }

  &__bottom {
    position: fixed;
  }
}
